import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as models from '../../models';

@IonicPage({
  name: 'ReadingChartPage',
  segment: 'reading-chart'
})
@Component({
  selector: 'reading-chart',
  templateUrl: 'reading-chart.html'
})
export class ReadingChartPage {

  book: models.AppReadingBook;

  items: Array<any>;
  groups: Array<any>;
  options: any;

  constructor(
    private params: NavParams,
    private navCtrl: NavController
  ) {
    if (params.data) {
      this.book = params.data;
    }

    this.items = [
      // { x: '2014-06-11T20:00+09:00', end: '2014-06-11T22:00+09:00', y: 30, group: 0 },
      // { x: '2014-06-12T21:00+09:00', end: '2014-06-12T22:00+09:00', y: 25, group: 0 },
      // { x: '2014-06-13T22:00+09:00', end: '2014-06-13T22:00+09:00', y: 100, group: 0 },
      // { x: '2014-06-14T23:00+09:00', end: '2014-06-14T23:10+09:00', y: 28, group: 0 },
      // { x: '2014-06-15T01:00+09:00', end: '2014-06-15T02:00+09:00', y: 66, group: 0 },
      // { x: '2014-06-16T03:00+09:00', end: '2014-06-16T07:00+09:00', y: 90, group: 0 },

      // { x: '2014-06-11T22:00+09:00', y: 10, group: 1 },
      // { x: '2014-06-12T22:00+09:00', y: 35, group: 1 },
      // { x: '2014-06-13T22:00+09:00', y: 65, group: 1 },
      // { x: '2014-06-14T23:10+09:00', y: 75, group: 1 },
      // { x: '2014-06-15T02:00+09:00', y: 90, group: 1 },
      // { x: '2014-06-16T07:00+09:00', y: 95, group: 1 },
    ];

    let totalPageRange = new models.PageRanges();
    this.book.getReadPageRanges().forEach(readPage => {
      totalPageRange.margeAll(readPage.pageRanges);

      this.items.push({
        x: readPage.startDate,
        end: readPage.endDate,
        y: readPage.pageRanges.getReadPages(),
        group: 0
      })

      this.items.push({
        x: readPage.endDate,
        y: totalPageRange.getReadPages(),
        group: 1
      })
    });

    this.groups = [
      {
        id: 0,
        content: '読書ページ数',
        options: {
          style:'bar'
        }
      },
      {
        id: 1,
        content: '読書済みページ数',
        options: {
          // yAxisOrientation: 'right'
        }
      }
    ];

    let minDate = this.book.getMinReadingDate();
    let maxDate = this.book.getMaxReadingDate();

    let startDate = new Date(
      minDate.getFullYear(), minDate.getMonth(), minDate.getDate(), 0, 0, 0, 0);

    let endDate = new Date(
      maxDate.getFullYear(), maxDate.getMonth(), maxDate.getDate(), 23, 59, 59, 999);

    this.options = {
      width:  '100%',
      legend: {
        left:{
          position:"top-left"
        },
        right:{
          position:"top-right"
        }
      },
      dataAxis: {
        left: {
          range: {
            min: 0, max: this.book.pages
          }
        }
      },
      drawPoints: false,
      interpolation: {
        enabled: false,
      },
      // locale: navigator.language,
      start: startDate,
      end: endDate
    };
  }
}



