import { Component, Input, ElementRef } from '@angular/core';
import { DateUtils } from '../../utils/DateUtils'
// import * as vis from 'vis';
declare var vis: any;

@Component({
  selector: 'chart',
  templateUrl: 'chart.html'
})
export class ChartComponent {

  @Input() style: string;

  @Input() chartId: string;

  @Input() items: any;

  @Input() groups?: any = {};

  @Input() options: any;

  initialized: boolean;

  constructor(public el: ElementRef) {
  }

  ngAfterViewInit() {
    this.initChart();
    this.initialized = true;
  }

  initChart() {
    let container = this.el.nativeElement.children[0];

    this.options.moment = function(date) {
      return vis.moment(date).utcOffset(DateUtils.getTimezoneOffsetStr());
    }

    let graph2d = new vis.Graph2d(
      container, 
      new vis.DataSet(this.items),
      new vis.DataSet(this.groups),
      this.options);
  }
}



